<template>
	<button class="loginButton" style="margin: 0 auto;" open-type="getPhoneNumber" @getphonenumber="getPhoneNumber">授权登录</button>
</template>

<script>
import { authorizedLogin } from '../../api/login.js';
export default {
	props: {
		loginCode: {
			type: String,
			default: ''
		}
	},
	data() {
		return {};
	},
	methods: {
		getPhoneNumber(res) {
			authorizedLogin({
				code: this.loginCode,
				encryptedData: res.detail.encryptedData,
				offset: res.detail.iv,
				source: 'CLIENT',
				grantParty: 'WECHER_APPLET'
			}).then(loginRes => {
				uni.setStorageSync('userInfo', loginRes.data);
				return loginRes;
			});
		}
	}
};
</script>

<style>
.loginButton {
	width: 600rpx;
	height: 90rpx;
	background: #4588e5;
	border-radius: 45rpx;
	font-size: 36rpx;
	text-align: center;
	line-height: 90rpx;
	color: #fff;
}
</style>
